.ultag{
  background-color: #5ec9e7;
  padding: 5px;
  margin:5px auto;
}
.ultag li{
  display: inline-block;
  padding: 15px;
}
.ultag li a{
 
  color:#fff;
  padding:5px;
  text-decoration: none;
}
.Footer{
  position: absolute;
  bottom:0;
  text-align: center;
  background-color: #5ec9e7;
 width:100%;
}
.signin{
  text-align: center;
  margin : 30px auto;
  font-size: 25px;
  font-family: serif;
}
.inputBox{
  display: block;
  text-align: center;
  font-size: 20px;
  font-family: serif;
  margin: 25px auto;
  padding: 30px;
  width: 30%;
  border: 1px solid rebeccapurple;
}
.button{
  font-size: 25px;
  margin: 40px auto;
  padding:20px;
  background-color: #5ec9e7;
  border: 1px solid rebeccapurple;
  cursor: pointer;
}
.Login{

  margin : 30px auto;
  text-align: center;
  margin-top: 10%;
  padding-bottom:2% ;
}
.App {
  text-align: center;
}
.nav-right{
  text-align: right;
  margin-top: 20px;
  font-size: 20px;
}
.logo{
  width:130px;
  float: left;
  border-radius: 8%;
  margin-top: 0px;
  margin-left: 20px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
